import * as path from 'path';
import * as yargs from 'yargs';
import * as gulp from 'gulp';

import sass from '@microsoft/gulp-core-build-sass';
import { serve, reload, trustDevCert, untrustDevCert } from '@microsoft/gulp-core-build-serve';
import webpack from '@microsoft/gulp-core-build-webpack';
import karma from '@microsoft/gulp-core-build-karma';
import * as spBuildCoreTasks from '@microsoft/sp-build-core-tasks';

import {
  SPBuildRig,
  BuildRigConstants,
  ITaskDefinition,
  IExecutable,
  serial,
  parallel,
  watch,
  ISPBuildRigArgs
} from '@microsoft/sp-build-common';

import { WebBuildRigConstants } from './WebBuildRigConstants';

export interface ISPWebBuildRigArgs extends ISPBuildRigArgs {
  debug: boolean;
}

function getSchemaFilePath(filename: string): string {
  return path.join(__dirname, 'schemas', filename);
}

export class SPWebBuildRig extends SPBuildRig {
  protected args: ISPBuildRigArgs;

  public initialize(gulp: gulp.Gulp): void {
    super.initialize(gulp);
  }

  protected getYargs(): yargs.Argv {
    // tslint:disable:max-line-length
    return super.getYargs()
      .option('debug', {
        describe: 'runs tests in unit mode'
      })
      .command(WebBuildRigConstants.tasks.bundle, 'build, localize, and bundle the project')
      .command(WebBuildRigConstants.tasks.deployAzureStorage, 'upload the assets to a development CDN')
      .command(WebBuildRigConstants.tasks.packageSolution, 'package the project into a SPAPP')
      .command(WebBuildRigConstants.tasks.test, 'build, localize, and bundle the project and run tests, and verify the coverage')
      .command(WebBuildRigConstants.tasks.serve, 'build and bundle the project and run the development server')
      .command(WebBuildRigConstants.tasks.devDeploy, 'deploy the current project to a development Azure CDN for sharing builds with colleagues.')
      .command(WebBuildRigConstants.tasks.trustDevCert, 'generates and trusts a development certificate if one isn\'t already present')
      .command(WebBuildRigConstants.tasks.untrustDevCert, 'untrusts and deletes the development certificate if it exists')
      .command('default', 'equivalent to bundle and test');
    // tslint:enable:max-line-length
  }

  protected setupSharedConfig(): void {
    super.setupSharedConfig();

    sass.mergeConfig({
      dropCssFiles: true,  // Drops .css files in the lib directory for webpack,
      warnOnNonCSSModules: true
    });

    karma.mergeConfig({
      configPath: path.resolve(path.join(__dirname, 'karma', 'karma.config.js'))
    });

    spBuildCoreTasks.configureWebpack.mergeConfig({
      webpack: webpack,
      configureExternalBundlingWebpackTask: spBuildCoreTasks.configureExternalBundlingWebpack
    });

    spBuildCoreTasks.configureExternalBundlingWebpack.mergeConfig({
      webpack: webpack,
      configureWebpackTask: spBuildCoreTasks.configureWebpack,
      debugLocale: this.args.locale
    });

    spBuildCoreTasks.writeManifests.mergeConfig({
      debugLocale: this.args.locale
    });

    if (this.args.production) {
      spBuildCoreTasks.copyAssets.mergeConfig({
        extsToIgnore: ['.map', '.stats.json', '.stats.html']
      });
    }
  }

  protected finalizeSharedConfig(): void {
    super.finalizeSharedConfig();
    if (this.args.minimal) {
      this._disableTasks(karma, spBuildCoreTasks.casperJSRunner);
    }

    spBuildCoreTasks.writeManifests.mergeConfig({
      deployCdnPath: spBuildCoreTasks.copyAssets.taskConfig.deployCdnPath,
      debugBasePath:
        `${serve.taskConfig.https ? 'https' : 'http'}://localhost:${serve.taskConfig.port}/`
    });

    if (this.args.production) {
      spBuildCoreTasks.packageSolution.mergeConfig({
        paths: {
          manifestDir: spBuildCoreTasks.copyAssets.taskConfig.deployCdnPath
        }
      });
    }
  }

  protected getTasks(): Map<string, ITaskDefinition> {
    const result: Map<string, ITaskDefinition> = super.getTasks();

    result.set(BuildRigConstants.tasks.build, {
      executable: this.getBuildTask()
    });

    result.set(WebBuildRigConstants.tasks.bundle, {
      executable: this.getBundleTask()
    });

    result.set(WebBuildRigConstants.tasks.devDeploy, {
      executable: spBuildCoreTasks.devDeploy,
      arguments: (yargs: yargs.Argv) => {
        return yargs
          .option('rush', {
            describe: 'use all projects specified in the repo\'s rush.json'
          });
      }
    });

    result.set(WebBuildRigConstants.tasks.deployAzureStorage, {
      executable: spBuildCoreTasks.deployAzureStorage
    });
    result.set(WebBuildRigConstants.tasks.packageSolution, {
      executable: spBuildCoreTasks.packageSolution
    });

    // @todo VSO #167343
    result.set(WebBuildRigConstants.tasks.test, {
      executable: this.getTestTask(),
      arguments: (yargs: yargs.Argv) => {
        return yargs
          .option('debug', {
            describe: 'run tests in debug mode'
          })
          .option('match', {
            describe: 'regular expression. Only run tests that match',
            string: true
          });
      }
    });

    result.set(WebBuildRigConstants.tasks.serve, {
      executable: serial(result.get(WebBuildRigConstants.tasks.bundle).executable,
        serve,
        watch(['src/**/*.{ts,tsx,scss,resx,js,json,html}',
          '!src/**/*.{scss.ts,resx.ts}'],
          serial(result.get(WebBuildRigConstants.tasks.bundle).executable, reload))),
      arguments: (yargs: yargs.Argv) => {
        return yargs
          .option('port', {
            description: 'the port to serve on should be the next argument (e.g. "--port 80")'
          })
          .option('nobrowser', {
            description: 'don\'t open a browser after initial bundle'
          });
      }
    });

    result.set(WebBuildRigConstants.tasks.trustDevCert, {
      executable: trustDevCert
    });

    result.set(WebBuildRigConstants.tasks.untrustDevCert, {
      executable: untrustDevCert
    });

    result.set(BuildRigConstants.tasks.default, result.get(WebBuildRigConstants.tasks.bundle));

    return result;
  }

  protected getTestTask(): IExecutable {
    return serial(this.getBundleTask(),
                  (WebBuildRigConstants.flags.enableCasperTests
                    ? parallel(spBuildCoreTasks.casperJSRunner, karma)
                    : karma) as IExecutable);
  }

  protected getCoreBuildTask(): IExecutable {
    return parallel(
      serial(sass, super.getCoreBuildTask()),
      spBuildCoreTasks.copyStaticAssets);
  }

  protected getBundleTask(): IExecutable {
    return serial(this.getBuildTask(),
                  spBuildCoreTasks.collectLocalizedResources,
                  spBuildCoreTasks.configureWebpack,
                  webpack, // First webpack instance to create the base bundle
                  spBuildCoreTasks.configureExternalBundlingWebpack,
                  // Second webpack instance to optionally expand the base bundle. This task is disabled if
                  //  configureExternalBundlingWebpack isn't configured to do anything
                  webpack,
                  spBuildCoreTasks.copyAssets,
                  spBuildCoreTasks.writeManifests);
  }
}